@import url(https://fonts.googleapis.com/css?family=Lato);
* {
  box-sizing: border-box;
}

.info {
  margin-top: 2em;
  text-align: center;
  font-family: "Lato", sans-serif;
}

.wrapper {
  width: 85%;
  max-width: 1000px;
  margin: 0.5em auto;
  font-family: "Lato", sans-serif;
}

.intro {
  text-align: center;
}

.intro h1 {
  font-size: 1.5em;
}

.intro p {
  font-size: 1em;
  margin-bottom: 1.2em;
}

.is-striped {
  background-color: #ebedea;
}

.crew-cell {
  width: 1%;
}
.name-cell {
  width: 30%;
}
.passengers-cell {
  width: 1%;
}
.hyperdrive-cell {
  width: 1%;
}

.rtable {
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 3em 0;
  padding: 0;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.1);
  font-size: 1em;
}
.rtable .rtable-row {
  width: 100%;
  display: flex;
}
.rtable .rtable-row .rtable-cell {
  box-sizing: border-box;
  flex-grow: 1;
  padding: 0.8em 1.2em;
  overflow: hidden;
  list-style: none;
}
.rtable .rtable-row .rtable-cell.column-heading {
  background-color: #979183;
  color: #ffffff;
  font-weight: 700;
  padding: 1em;
}

.rtable .rtable-row .rtable-cell .rtable-cell--heading {
  display: none;
}

.App-link {
  color: #db3b72;
  outline: none;
  text-decoration: none;
}

.App-link:visited {
  color: #db3b72;
}

.App-link:focus {
  border-bottom: 1px dotted;  
}

.App-link:hover {
  border-bottom: 1px dotted;     
}

.App-link:active {
  color: #4a0047;
}

@media all and (max-width: 700px) {
  .is-striped {
    background-color: #ffffff;
  }
  .rtable--collapse {
    display: block;
    width: 100%;
    padding: 1em;
    box-shadow: none;
  }
  .rtable--collapse .rtable-row {
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 2em;
    box-shadow: 0 0 40px rgba(0, 0, 0, 0.1); 
  }
  .rtable--collapse .rtable-row .rtable-cell {
    width: 100% !important;
    display: flex;
    align-items: center;
  }
  .rtable--collapse .rtable-row .rtable-cell .rtable-cell--heading {
    display: inline-block;
    flex: 1 1;
    max-width: 120px;
    min-width: 120px;
    color: #979183;
    font-weight: 700;
    border-right: 1px solid #ccc;
    margin-right: 1em;
  }
  .rtable--collapse .rtable-row .rtable-cell .rtable-cell--content {
    flex: 2 1;
    padding-left: 1em;
  }
  .rtable--collapse .name-cell {
    background-color: #979183;
    color: #ffffff;
    font-weight: 700;
    order: -1;
  }
  .rtable--collapse .name-cell .rtable-cell--content {
    padding-left: 0 !important;
  }
  .rtable--collapse .rtable-row--head {
    display: none;
  }
}
.no-flexbox .rtable {
  display: block;
}
.no-flexbox .rtable.rtable-cell {
  width: 100%;
}

